import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

// 1.  () 
//  import 
import Help from './components/Help.vue'
import ListUser from './components/ListUser.vue'
import AddUser from './components/AddUser.vue'
import EditUser from './components/EditUser.vue'
import Index from "./components/Index";
import ShowUser from "./components/ShowUser";
import ListBlog from "./components/ListBlog";
import AddBlog from "./components/AddBlog";
import ShowBlog from "./components/ShowBlog";
import EditBlog from "./components/EditBlog";

// 2. 
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [
  {path: '/', component: Index, meta: {title: 'Demo '}},
  {path: '/user/list', component: ListUser, meta: {title: ''}},
  {path: '/user/show/:id', component: ShowUser, meta: {title: ''}},
  {path: '/user/add', component: AddUser, meta: {title: ""}},
  {path: '/user/edit/:id', component: EditUser, meta: {title: ''}},
  {path: '/blog/list', component: ListBlog, meta: {title: ''}},
  {path: '/blog/add', component: AddBlog, meta: {title: ''}},
  {path: '/blog/show/:id', component: ShowBlog, meta: {title: ''}},
  {path: '/blog/edit/:id', component: EditBlog, meta: {title: ''}},
  {path: '/help', component: Help}
];

// 3.  router  `routes` 
// , 
const router = new VueRouter({
  routes // ()  routes: routes
});

const DEFAULT_TITLE = 'Demo';
// eslint-disable-next-line
router.afterEach((to, from) => {
  document.title = to.meta.title || DEFAULT_TITLE;
});

export default router