<template>
  <div class="container">
    <h1>-</h1>
    <hr/>

    <h3> <router-link to="/blog/add" type="button" class="btn btn-primary btn-sm"></router-link></h3>

    <!--  -->
    <div class="alert alert-warning" role="alert" v-if="blogList.length == 0">
      <span class="glyphicon glyphicon-info-sign" aria-hidden="true"></span>Blog<router-link to="/blog/add" type="button" class="btn btn-primary btn-sm"></router-link>
    </div>

    <!--  -->
    <table class="table table-bordered table-striped" v-if="blogList.length > 0">
      <tbody>
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>

        <template v-for="blog in blogList">
          <tr v-bind:key="blog.id">
            <td>{{blog.id}}</td>
            <td>{{blog.title}}</td>
            <td>{{blog.nickname}}, {{blog.firstName}} {{blog.lastName}}</td>
            <td>{{ blog.pubDate | moment("YYYYMMDD") }}</td>
            <td>
              <router-link :to="'/blog/show/' + blog.id" type="button" class="btn btn-sm btn-success"></router-link> &nbsp;
              <router-link :to="'/blog/edit/' + blog.id" type="button" class="btn btn-sm btn-warning"></router-link> &nbsp;
              <a v-on:click="deleteBlog(blog.id)" type="button" class="btn btn-sm btn-danger" ></a>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
import Vue from 'vue'
import BlogService from "../services/BlogService";

export default {
  name: 'ListUser',
  methods: {
      deleteBlog: function (blogId) {
          BlogService.deleteBlog({blogId: blogId}, () => {this.getBlogList();}, errMsg => {Vue.console.error(errMsg)})
      },
      getBlogList: function() {
          BlogService.getBlogList({}, blogList => { this.blogList = blogList; }, errMsg => { Vue.console.error(errMsg); })
      }
  },
  data: function() {
    return {
      blogList: []
    }
  },
  mounted: function() {
    this.getBlogList();
  }
}
</script>

